$color: (
  rgb(253, 253, 255)
);

:root {
  background-color: lightgray;
}

.main-view {
  color: rgb(116, 116, 116);
}

.movie-card {
  background-color: rgb(0, 0, 0);
  cursor: pointer;
}

.movie-view {
  background-color: rgb(0, 0, 0);
}

.login-view {
  background-color: rgb(116, 116, 116);
  display: grid;
}

form {
  background-color: rgb(104, 100, 100);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
}

form label {
  padding: .5em;
}